      "description": "<font size='3'>This layer provides 3D models of buildings for Portland, Oregon USA to support your work 3D and provides a technical preview of 3D streaming capabilities of ArcGIS 10.3.  You can use this layer  ArcGIS Pro projects or the Online Scene viewer to\r\n visualize your maps and layers within the context of the built \r\nenvironment.  This layer includes data provided by the City of Portland, Oregon USA.  <br \/><br \/><\/font>\r\n<font size='3'><span style='font-weight: bold;'><span style='color: rgb(178, 34, 34);'>Note:<\/span><\/span> The\r\nscene layer of buildings is being published by Esri using an early, internal\r\nrelease of 10.3.1.  Users will be able to publish similar\r\nbuildings and other features based on geometry meshes (aka multipatch\r\ngeometry) using ArcGIS for Server when this capability is\r\nreleased in 2015.<\/font>",
