      "description": "<span style='overflow: auto;'><font size='4'><span style='font-family: Tahoma;'>This\r\n scene highlights layers for Portland, Oregon available in ArcGIS to \r\nsupport your work in 3D.  Use these layers in conjunction with your own \r\nlayers to create new scenes focused on a specific topic or area of \r\ninterest to you.  What\r\n\r\n is a scene?  Think of it as a map for working in 3D but with \r\nadditional settings for things like sun angle.<\/span><br \/><br \/><\/font><\/span><font size='3' style='font-family: Tahoma;'><span style='overflow: auto;'><span><font size='4'>What's in this scene? <\/font><br \/>1)\r\n Terrain: includes a global 3D terrain layer to provide \r\nelevation context.  Your layers are placed in relationship to this \r\nterrain<br \/>2) Basemap: includes one of the ArcGIS Basemaps regularly used in in your mapping work<br \/>3) Scene Layers<\/span><\/span>: includes a layer<\/font><font size='3'>\r\n of 3D buildings to help understand your data within the context of the \r\nbuilt environment.  The layer is a file type optimized for rendering in \r\n3D.<br \/><br \/>This scene has pop ups available for the Buildings layer to provide useful information<br \/><img alt='Buildings Layer Pop Up' src='http:\/\/arcgis.com\/sharing\/rest\/content\/items\/5204857c4ca141748928a2dbc40e4515\/data' \/><\/font><br \/><font size='3'><span style='overflow: auto;'><font size='4'><font face='Tahoma'><br \/>Create your own scene:<\/font><br \/><font size='3' style='font-family: Tahoma;'>1) Sign in with your ArcGIS account and create new scene<\/font><\/font><\/span><\/font><br \/><font size='3'><span style='overflow: auto;'><font size='4'><font size='3' style='font-family: Tahoma;'>2) Add this scene: Click ADD LAYERS and click Import<\/font><\/font><\/span><\/font><div style='margin-left: 40px;'><img alt='Esri Featured Content' src='http:\/\/arcgis.com\/sharing\/rest\/content\/items\/49f8084f5a4648328fac4711f955d8c7\/data' \/><br \/><\/div><font size='3'><span style='overflow: auto;'><font size='4'><font size='3' style='font-family: Tahoma;'>3) Choose basemap: Select one of the ArcGIS basemaps from the Basemap Gallery<br \/><\/font><\/font><\/span><\/font><div style='margin-left: 40px;'><img alt='Scene Basemap Gallery' src='http:\/\/arcgis.com\/sharing\/rest\/content\/items\/7421ffb325504b8289dd061bcf8a068e\/data' \/><\/div><font size='3'><span style='overflow: auto;'><font size='4'><font size='3' style='font-family: Tahoma;'>4) Add your own unique layers <\/font><\/font><\/span><\/font><font size='3'><span style='overflow: auto;'><font size='4'><font size='3' style='font-family: Tahoma;'><font size='3'><span style='overflow: auto;'><font size='4'><font size='3' style='font-family: Tahoma;'>(Note: publishing multipatch features like 3D buildings will be supported in the 10.3.1 release of ArcGIS)<br \/><\/font><\/font><\/span><\/font>5) Create slides to direct users to interesting places in your scene - <a href='http:\/\/video.esri.com\/watch\/3981\/author-web-scenes-using-arcgis-online' target='_blank'>See More<\/a><br \/>6) Save and share the results of your work with others in your organization and the \r\npublic<\/font><\/font><\/span><br \/><br \/><\/font><font size='3'><font size='3'><span style='font-weight: bold;'>Examples<\/span> <br \/>Gain inspiration from these examples to help you create your own scene<\/font><br \/><a href='http:\/\/qaext.arcgis.com\/home\/item.html?id=220029e0ec714403934c473faee1eec9' target='_blank'>Visualize Urban Development<\/a><br \/><a href='http:\/\/qaext.arcgis.com\/home\/item.html?id=1dc308ae1e65421292e8b04813d59e48' target='_blank'>Perform Solar Analysis<\/a><br \/><br \/>For more see these helpful videos:<br \/><span style='color: rgb(0, 0, 238);'><span style='font-weight: bold;'><a href='http:\/\/video.esri.com\/watch\/3991\/mashup-3d-content-using-arcgis-online' target='_blank'>Mashup 3D Content Using ArcGIS Online<\/a><br \/><a href='http:\/\/video.esri.com\/watch\/3981\/author-web-scenes-using-arcgis-online' target='_blank'>Author Web Scenes Using ArcGIS Online<\/a><\/span><\/span><\/font><span style='line-height: 22.153844833374px; color: rgb(0, 0, 0); font-size: medium;'><\/span>",
