      "description": "<span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><p><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span><span>This  layer package can be used by ArcGIS Desktop 9.3.1 or 10 users. The  services it contains are stored as a high performance basemap layer which gives you  the best drawing speed in ArcGIS 10. <\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span>(This basemap layer appears as a normal group layer when opened in 9.3.1.)<\/p><p>This layer package\u00a0<span style='overflow: auto;'><span>provides one meter or better satellite and aerial imagery in many parts of the world and lower resolution satellite imagery worldwide with\u00a0transportation,\u00a0political boundaries and place names for reference purposes. <\/span><\/span><span style='overflow: auto;'><span>For more details, visit the <a href='http:\/\/www.arcgis.com\/home\/item.html?id=10df2279f9684e4a9f6a7f08febac2a9'>World Imagery<\/a>,\u00a0<a href='http:\/\/www.arcgis.com\/home\/item.html?id=94f838a535334cf1aa061846514b77c7' target='_self'>World Transportation<\/a>,\u00a0and <a href='http:\/\/www.arcgis.com\/home\/item.html?id=a842e359856a4365b1ddf8cc34fde079'>World Boundaries and Places<\/a> map service descriptions.<\/span><\/span><\/p><span style='display: block;'><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span>",
