      "spatialReference": null,
      "accessInformation": "Colorado Parks and Wildlife",
      "licenseInfo": "<p style='MARGIN: 0in 0in 10pt'><span><span style='LINE-height: 115%'>This wildlife distribution map is a product and property of Colorado Parks and\u00a0Wildlife, a division of the Colorado Department of Natural Resources. Care should be taken in interpreting these data. Written documents may accompany this map and should be referenced. The information portrayed on these maps should not replace field studies necessary for more localized planning efforts. The data are gathered at a variety of scales; discrepancies may become apparent at larger scales. The areas portrayed here are graphic representations of phenomena that are difficult to reduce to two dimensions. Animal distributions are fluid; animal populations and their habitats are dynamic. <p><\/p><\/span><\/span><\/p>",
