      "description": "The World Geocoding Service finds addresses and places in all <a href='http:\/\/links.esri.com\/arcgis-online-data-coverage-for-geocoding' target='_blank'>supported countries<\/a> around the world in a single geocoding service. The service can find point locations of  addresses, cities, landmarks, business names, and other places. The output points can be  visualized on a map, inserted as stops for a route, or loaded as input  for a spatial analysis.<div><br \/><\/div><div>The service is available as both a geosearch and geocoding service:<br \/><div><ul><li><b>Geosearch Services<\/b><span style='line-height: 1.38461538461538; text-indent: -0.25in;'>  \u2013 The primary purpose of geosearch services is to locate a feature or  point of interest and then have the map zoom to that location. The  result might be displayed on the map, but the result is not stored in  any way for later use. Requests of this type do not require a subscription or a credit fee. \u00a0<\/span><\/li><li><b>Geocoding Services<\/b><span style='text-indent: -0.25in; line-height: 1.38461538461538;'>  \u2013 The primary purpose of geocoding services is to convert an address to  an x,y coordinate and append the result to an existing record in a  database. Mapping is not always involved, but placing the results on a  map may be part of a workflow. Batch geocoding falls into this  category.\u00a0<\/span><b>Geocoding requires a subscription.<\/b><span style='text-indent: -0.25in; line-height: 1.38461538461538;'>\u00a0An <\/span><a href='http:\/\/www.esri.com\/software\/arcgis\/arcgisonline' style='text-indent: -0.25in; line-height: 1.38461538461538;' target='_blank'>ArcGIS Online subscription<\/a><span style='text-indent: -0.25in; line-height: 1.38461538461538;'> will  provide you access to the World Geocoding service for batch geocoding.<\/span><\/li><\/ul><span style='overflow: auto;'><span><font color='#4d4d4d'><span style='line-height: 17.899999618530273px;'>The service can be used to find address and places for many countries around the world. For detailed information on this service, including a data coverage map, visit the <\/span><\/font><\/span><\/span><a href='http:\/\/links.esri.com\/arcgis-online-geocoding-rest-api\/' target='_blank'>World Geocoding service documentation<\/a><span style='overflow: auto;'><span><font color='#4d4d4d'><span style='line-height: 17.899999618530273px;'>.<\/span><\/font><\/span><\/span><\/div><\/div>",
