      "name": null,
      "title": "USA Food Expenditures at Home vs. Away from Home",
      "type": "Web Map",
      "typology": "map",
      "typeKeywords": [
        "ArcGIS Online",
        "Explorer Web Map",
        "Map",
        "Online Map",
        "Web Map"
      ],
      "description": "<span><span><span><span><span><span><span><span><span><p>This web map displays a ratio of the average annual household expenditure on &quot;food at home&quot; to &quot;food away from home.&quot;  Some of the patterns are quite interesting.<br /><\/p><p>Red area households spend noticeably more at home, blue area households spend noticeably more away from home. Households in an &quot;average&quot; area tend to spend $1.38 on food at home for every $1.00 on food away from home.  Red areas are above this average, and blue areas are below this average.  Yellow areas are average.<br /><\/p><p><span><span><span><span>Data is shown at the county, tract and block group levels using consistent colors and classifications.  Put this map in your web browser or <a href='http://www.esri.com/software/arcgis/ios/index.html' target='_blank'>Esri iPhone/iPad app.<\/a><\/span><\/span><\/span><\/span><\/p><p>There's a very nice <a href='http://www.visualeconomics.com/how-the-average-us-consumer-spends-their-paycheck/' target='_blank'>visual of consumer expenditures data here<\/a>.<\/p><p><span><span><span><span><span><span>These are just two of the <a href='http://www.esri.com/data/esri_data/pdfs/2010-esri-consumer-spending-data-list.pdf' target='_blank'>hundreds of variables<\/a> available in the Esri Consumer Spending database.  <br /><\/span><\/span><\/span><\/span><\/span><\/span><\/p><p><span><span><span><span><span><span><\/span><\/span><\/span><\/span><\/span><\/span>Esri combined the 2005-2006 <a href='http://www.bls.gov/cex/home.htm' target='_blank'>Consumer Expenditure Surveys from the Bureau of Labor Statistics<\/a> to estimate current spending patterns. The continuing surveys include a Diary Survey for daily purchases and an Interview Survey for general purchases. The Diary Survey represents record keeping by consumer units for two consecutive weeklong periods. This component collects data on small daily purchases that could be overlooked by the quarterly Interview Survey. The Interview Survey collects expenditure data from consumers in five interviews conducted every three months. <br /><\/p><p>Esri integrates data from both surveys to provide a comprehensive database on all consumer expenditures. To compensate for the relatively small survey bases and the variability of single-year data, expenditures are averaged from the 2005\u20132006 surveys.<\/p>Esri's 2010<b> <\/b>Consumer Spending database details which products and services area consumers buy. Updated annually, the Consumer Spending database contains more than 760 items in 15 categories such as apparel, food, and financial. The database includes information about total dollars spent, the average amount spent by household, and a Spending Potential Index that compares local average product expenditures to the national average.<br /><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span><\/span>",
