import most from 'most'
import virtualize from 'vdom-virtualize'
import template from './view.dot'
import render from '../lib/render.js'
import calcite from 'calcite-web'
import dict from './data/item-actions-dict'

function parseState (state) {
  let items = state.items.map(item => {
    state.selected.forEach(obj => {
      if (obj.id == item.id) {
       item.selected = true
      }
    })
    state.hidden.forEach(obj => {
      if (obj.id == item.id) {
       item.hidden = true
      }
    })
    return item
  })
  state.items = items
  return state
}

function parseActions (state) {
  let actions = []
  let selectedTypes = []

  state.selected.forEach(i => {
    selectedTypes.push(i.type)
  })
  dict.actions.forEach( i => {
    actions.push(i)
  })
  dict.types.forEach(type => {
    if (selectedTypes.indexOf(type.name) > -1) {
      type.excludes.forEach( x => {
        let i = actions.indexOf(x);
        if (i > -1) {
            actions.splice(i, 1);
        }
      })
    }
  })
  state.actions = actions
  return state
}

/**
* Render current state to DOM Node
* @param  {Stream}  model   Event stream of changes to model
* @param  {Element} domNode Render target for view (DOM Element)
*/
function getView (model, domNode) {
  let tree = virtualize(domNode)
  model.forEach(state => {
    parseState(state)
    parseActions(state)
    let html = template(state)
    tree = render(domNode, html, tree)
    calcite.dropdown(domNode)
  })
}

export default getView
