import most from 'most'
import Store from '../lib/store.js'
import sortOn from '../lib/sort-on.js'
import calcite from 'calcite-web'

console.log(calcite)

// set up app store
let store
let nameAscending
let typeAscending
let dateAscending
let permsAscending

// wrap store events as a most stream
function publisher (add, end, error) {
  store.on('changed', state => {
    add(state)
  })
}

/**
* Open the description of an item
*/
function previewItem (id) {
  let state = store()
  state.items = state.items.map(i => {
    if (i.preview && i.id === id) {
      i.preview = false
    } else {
      i.preview = (i.id === id)
    }
    return i
  })
  store(state)
}

/**
* Filter Content List
*/
function filterInput (term) {
  let state = store()
  state.hidden = state.items.filter(item => {
    let relevant = `${item.title} ${item.owner} ${item.name} ${item.type} ${item.tags}`
    let filterBy = relevant.toLowerCase()
    let isHidden = filterBy.indexOf(term.toLowerCase()) < 0
    return isHidden
  })
  store(state)
}

function filterType(e) {
  let state = store()
  // console.log(e.target)
  calcite.toggleClass(e.target, 'is-active')
  state.hidden = state.items.filter(item => {
    let isHidden = item.typology.indexOf(e.filter.toLowerCase()) < 0
    return isHidden
  })
  store(state)
}

function clearInput () {
  let state = store()
  document.querySelector('.js-content-filter').value = ''
  state.hidden = []
  store(state)
}


/**
* Select / Deselect Items
*/
function toggleSelect (row) {
  let state = store()
  state.selected = state.selected.filter(obj => obj.id !== row.id)
  if (row.selected) {
    state.selected.push({
      id: row.id,
      type: row.type
    })
  }
  if (state.selected.length) {
    document.querySelector('.js-content-select-all').indeterminate = true
  } else {
    document.querySelector('.js-content-select-all').indeterminate = false
  }
  store(state)
}


function toggleSelectAll (e) {
  let state = store()
  let selectAll = e.target.checked
  let unselectable = []

  if (!selectAll) {
    state.selected = []
  } else {

    state.hidden.forEach(item => {
      unselectable.push(item.id)
    })

    state.items.forEach(item => {
      if (unselectable.indexOf(item.id) == -1) {
        state.selected.push({
          id: item.id,
          type: item.type
        })
      }
    })

    if (unselectable.length) {
      document.querySelector('.js-content-select-all').indeterminate = true
    } else {
      document.querySelector('.js-content-select-all').indeterminate = false
    }
  }

  store(state)
}


/**
* Handle sorting data by ascending, descending, across 4 columns with tiebreaker key
*/
function sortName () {
  let state = store()
  let ascending = sortOn('title', 'modified')
  let descending = sortOn('title', 'modified', true)
  if (nameAscending) {
    state.items = state.items.sort(descending)
    nameAscending = false
  } else {
    state.items = state.items.sort(ascending)
    nameAscending = true
  }
  store(state)
}
function sortDate () {
  let state = store()
  let ascending = sortOn('modified', 'title')
  let descending = sortOn('modified', 'title', true)
  if (dateAscending) {
    state.items = state.items.sort(descending)
    dateAscending = false
  } else {
    state.items = state.items.sort(ascending)
    dateAscending = true
  }
  store(state)
}
function sortType () {
  let state = store()
  let ascending = sortOn('type', 'title')
  let descending = sortOn('type', 'title', true)
  if (typeAscending) {
    state.items = state.items.sort(descending)
    typeAscending = false
  } else {
    state.items = state.items.sort(ascending)
    typeAscending = true
  }
  store(state)
}
function sortPerms () {
  let state = store()
  let ascending = sortOn('access', 'title')
  let descending = sortOn('access', 'title', true)
  if (typeAscending) {
    state.items = state.items.sort(descending)
    typeAscending = false
  } else {
    state.items = state.items.sort(ascending)
    typeAscending = true
  }
  store(state)
}

/**
* Observe intent streams
* export a stream of store updates
*/
function getModel (intent, Store) {
  store = Store
  intent.previewItem.observe(previewItem)
  intent.toggleSelect.observe(toggleSelect)
  intent.filterInput.observe(filterInput)
  intent.filterType.observe(filterType)
  intent.clearInput.observe(clearInput)
  intent.toggleSelectAll.observe(toggleSelectAll)
  intent.sortName.observe(sortName)
  intent.sortDate.observe(sortDate)
  intent.sortType.observe(sortType)
  intent.sortPerms.observe(sortPerms)
  let currentState = store()
  currentState.selected = []
  currentState.hidden = []
  currentState.items.sort(sortOn('modified', 'title'))
  dateAscending = true
  store(currentState)
  return most.create(publisher).startWith(currentState)
}

export default getModel
