/**
* Content Page
*/
import Store from './lib/store.js'
import Intent from './content/intent'
import Model from './content/model'
import View from './content/view'

let path = window.location.pathname

if (path.includes('organization-name')) {
  console.log(path)
  var data = require('./content/data/org-content')
  console.log(data)
} else if (path.includes('groups')) {
  // var data = require('./content/data/group-content')
  var data = require('./content/data/user-content')
} else if (path.includes('favorites')) {
  // var data = require('./content/data/favorite-content')
  var data = require('./content/data/user-content')
} else if (path.includes('atlas')) {
  // var data = require('./content/data/atlas-content')
  var data = require('./content/data/user-content')
} else {
  var data = require('./content/data/user-content')
}

data.selected = []
data.hidden = []

localStorage.clear()
let store = Store('user-content', data)

const ROOT = document.querySelector('.js-content-view')

let intent = Intent(ROOT)
let model = Model(intent, store)
let view = View(model, ROOT)

