import most from 'most'
import Delegate from '../lib/delegate.js'
import closest from 'dom-closest'

let getId = e => closest(e.target, '.js-row').getAttribute('data-id')
let getType = e => closest(e.target, '.js-row').getAttribute('data-type')
let getFilter = e => e.target.getAttribute('data-type')

function getIntent (domNode) {
  let delegate = Delegate(domNode)

  let toggleSelect = delegate('click', '.js-row-checkbox')
      .map(e => {
        // return { id: getId(e), selected: e.target.checked }
        return {
          id: getId(e),
          type: getType(e),
          selected: e.target.checked
        }
      })

  let filterInput = delegate('keyup', '.js-content-filter')
      .map(e => {
        return e.srcElement.value
      })

  let filterType = delegate('click', '.js-type')
      .map(e => {
      // return { id: getId(e), selected: e.target.checked }
      return {
        target: e.target,
        filter: getFilter(e)
      }
    })

  let clearInput = delegate('click', '.js-content-filter-clear')

  let toggleSelectAll = delegate('click', '.js-content-select-all')

  let sortName = delegate('click', '.js-order-name')
  let sortDate = delegate('click', '.js-order-date')
  let sortType = delegate('click', '.js-order-type')
  let sortPerms = delegate('click', '.js-order-permissions')

  let previewItem = delegate('click', '.js-preview-item')
  .tap(e => e.preventDefault())
  .map(getId)

  return {
    toggleSelect,
    filterInput,
    filterType,
    clearInput,
    toggleSelectAll,
    sortName,
    sortDate,
    sortType,
    sortPerms,
    previewItem
  }
}

export default getIntent

