function sortOn (key, key2, descending) {
  return function (a, b) {
    if (typeof a[key] == 'string') {
      if (a[key].toLowerCase() > b[key].toLowerCase()) {
        if (descending) {
          return -1
        }
        return 1;
      }
      if (a[key].toLowerCase() < b[key].toLowerCase()) {
        if (descending) {
          return 1
        }
        return -1;
      }
    } else {
      if (a[key] > b[key]) {
        if (descending) {
          return -1
        }
        return 1;
      }
      if (a[key] < b[key]) {
        if (descending) {
          return 1
        }
        return -1;
      }
    }

    if (typeof a[key2] == 'string') {
      if (a[key2].toLowerCase() > b[key2].toLowerCase()) {
        if (descending) {
          return -1
        }
        return 1;
      }
      if (a[key2].toLowerCase() < b[key2].toLowerCase()) {
        if (descending) {
          return 1
        }
        return -1;
      }
    } else {
      if (a[key2] > b[key2]) {
        if (descending) {
          return -1
        }
        return 1;
      }
      if (a[key2] < b[key2]) {
        if (descending) {
          return 1
        }
        return -1;
      }
    }


    return 0;
  }
}

export default sortOn